$buttonBackground : #006666;
$hoverTextColor : #101b1b;


.userListHeader {
  width: 92%;
  background-color: #f5f5f5 !important;
  margin-left: 50px !important;
}
.user-list-row-theme {
  width: 97%;
  margin-left: 0px !important;
  margin-right: 0px !important;
  padding-top: 1%;
}

.user-list-row-theme:hover {
 margin-left : 0px;
 background-color: #f5f5f5;
}  
.headernameStyle{
  width: fit-content;
  font-size: 11px;
  font-weight: 600;
}
.listElements {
  height: 35px;
  box-shadow: aliceblue;
  padding-top: 9px;
}
.listElementsHeader{ 
  margin-left: 40px;
  margin-top: 12px;
  font-weight: 700;
  font-size: 16px;
}
.userListElements {
  box-shadow: aliceblue;
  padding-top: 0px;
  display: flex;
  flex: 1;
  margin-top: 4px;
  flex-direction: row;
  align-items: center;

}
.userListElementsHeader{ 
  margin-left: 20px;
  margin-top: 12px;
  font-weight: 700;
  font-size: 16px;
}
.userListElementsHeader.role{ 
  margin-left: 22px !important;
}
.userProfileClass{
    border: 1px solid #bcbaba99;
    border-radius: 50%;
    margin-bottom:15px;
}
.userStatus{
border-radius: 14px;
padding: 3px 12px 3px 12px;
margin-left: 8px !important;
font-size: 12px;
color: #fff;
}
.userStatus.active{
  background-color: #3CB043;
}
.userStatus.inactive{
  background-color: #ff0000;
}

.setDelete {  
  
  margin-top: 8px;
  padding-left: 24px !important;
}

.setEdit {  
  
  margin-top: 8px;
  padding-left: 20px !important;
}


.addUserButtonDiv{
  float: right !important;
  margin-top: 0px;
  margin-left: 30px;
  margin-bottom: 12px;
  cursor: pointer;
}
.setIconButton {  
  margin-top:5px;
}
.add-user-text{
  font-size: 14px;
  font-weight: 500;
  margin-left: 2px;
}
.edit{
  
  padding-left: 53px !important;
}
.delete{
  
  padding-left: 72px !important;
}
.nameStyle{
    width: fit-content;
}
.userStyle{
  width: fit-content;
  padding-left: 6px;
  text-align: center;
  margin: 0px !important;
  font-size: 13px;
  margin-bottom:13px !important;
}

